<template>
  <div class="AdvertisingManagement">
    <div class="selectOpt">
      <el-form :inline="true" :model="formData" class="demo-form-inline">
        <el-form-item label="渠道">
          <el-select
            @change="changeOpt('channel')"
            v-model="formData.channel"
            placeholder="请选择渠道"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, index) in channels"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国家">
          <el-select
            @change="changeOpt('country')"
            v-model="formData.country"
            placeholder="请选择国家"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, index) in countries"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点">
          <el-select
            @change="changeOpt('store')"
            v-model="formData.store"
            placeholder="请选择站点"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, index) in stores"
              :key="index"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div class="contain">
      <div class="left-contain">
        <!-- active-text-color="#d90234"   default-active="1-1"-->
        <el-menu
          @select="selectMenu"
          unique-opened
          class="el-menu-vertical-demo"
          active-text-color="#d90234"
          default-active="1-1"
        >
          <el-submenu index="1">
            <template slot="title">
              <span>魔片</span>
            </template>
            <el-menu-item-group>
              <el-menu-item
                v-for="(item, i) in mp_list"
                :key="i"
                :index="'1-' + (i + 1)"
                >{{ item }}</el-menu-item
              >
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title">
              <span>钢木</span>
            </template>
            <el-menu-item-group>
              <el-menu-item
                v-for="(item, j) in gm_list"
                :key="j"
                :index="'2-' + (j + 1)"
                >{{ item }}</el-menu-item
              >
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </div>
      <div class="right-contain">
        <div
          :style="{ visibility: loading ? 'visible' : 'hidden' }"
          class="loadingMask"
        >
          <i class="el-icon-loading"></i><span>加载中...</span>
        </div>
        <div class="right-contain_box">
          <div class="operate_report">
            <span>运营日报</span>
            <table class="table">
              <thead>
                <tr>
                  <th>销售额</th>
                  <th>广告花费</th>
                  <th>广告比</th>
                </tr>
              </thead>
              <tbody>
                <tr v-if="!operate_report_table.length">
                  <td colspan="3">暂无数据</td>
                </tr>
                <template v-else>
                  <tr v-for="(item, i) in operate_report_table" :key="i">
                    <td>{{ item["yye"] }}</td>
                    <td>{{ item["gghf"] }}</td>
                    <td>{{ item["ggzb"] }}</td>
                  </tr>
                </template>
              </tbody>
            </table>
          </div>
          <div class="tables">
            <div class="score">
              <div class="score_head">
                <span class="firstlabel">评分</span>
                <div class="timepick">
                  <span>时间</span>
                  <!-- 默认显示7天数据 -->
                  <el-date-picker
                    @change="changeDate"
                    :picker-options="timePickerOptions"
                    value-format="yyyy-MM-dd"
                    v-model="dateValue1"
                    type="daterange"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                  >
                  </el-date-picker>
                </div>
                <div>
                  <span>30天内最高</span>
                  <span>{{ heighest_30 }}</span>
                  <span>30天内最低</span>
                  <span>{{ lowest_30 }}</span>
                </div>
              </div>
              <div class="table" v-show="score_table.length">
                <table>
                  <thead>
                    <tr>
                      <th></th>
                      <th v-for="(item, i) in score_table" :key="i">
                        <span> {{ item.dates }}</span>
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td><span>评分数量</span></td>
                      <td v-for="(item, j) in score_table" :key="j">
                        {{ item.comment_amount }}
                      </td>
                    </tr>
                    <tr>
                      <td>评分</td>
                      <td v-for="(item, j) in score_table" :key="j">
                        {{ item.star_level }}
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <div class="frontdestcheck">
              <div class="table">
                <table>
                  <thead>
                    <tr>
                      <th width="300px">sku</th>
                      <th width="150px">价格</th>
                      <th width="150px">首页差评</th>
                      <th width="150px">跟卖</th>
                      <th width="150px">5点描述</th>
                      <th width="150px">A+</th>
                      <th width="150px">链接拆分</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item, i) in AllData" :key="i">
                      <td>
                        <a
                          :class="{ active: i === curIndex }"
                          href="javascript:;"
                          @click="showSku(i, item.SKU)"
                          >{{ item.SKU }}</a
                        >
                      </td>
                      <td :class="{ showRed: item.price_bad == 1 }">
                        {{ item.price }}
                      </td>
                      <td>{{ item.bad_review }}</td>
                      <td
                        :class="{
                          showRed: item.sell_url && item.sell_url !== '无'
                        }"
                      >
                        {{ item.sell_url }}
                      </td>
                      <td>{{ item.five_describe }}</td>
                      <td>{{ item.A_page }}</td>
                      <td :class="{ showRed: item.link_break == '是' }">
                        {{ item.link_break }}
                      </td>
                    </tr>
                    <!-- <tr v-if="!AllData.length">
                    <td colspan="7">暂无数据</td>
                  </tr> -->
                  </tbody>
                </table>
              </div>
              <div class="right_table">
                <div class="rigth-top">
                  <div class="timepick">
                    <span>时间</span>
                    <!-- 默认显示7天数据 -->
                    <el-date-picker
                      @change="changeDate2"
                      :picker-options="timePickerOptions"
                      value-format="yyyy-MM-dd"
                      v-model="dateValue"
                      type="daterange"
                      range-separator="至"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期"
                    >
                    </el-date-picker>
                  </div>
                  <div class="min_max">
                    <span>30天内最高</span>
                    <span>{{ sku_heighest_30 }}</span>
                    <span>30天内最低</span>
                    <span>{{ sku_lowest_30 }}</span>
                    <span
                      ><a
                        :href="url"
                        style="color:blue;text-decoration:underline;"
                        target="_blank"
                        >点击链接查看</a
                      ></span
                    >
                  </div>
                </div>
                <div
                  class="line_table"
                  ref="chart1"
                  style="width: 90%; height: 500px;margin:0 auto"
                ></div>
              </div>
            </div>
            <div class="stock table-box">
              <span>库存</span>
              <table>
                <thead>
                  <tr>
                    <th>sku</th>
                    <th>品名</th>
                    <th>十日均单</th>
                    <th>总计</th>
                    <th>FBA库存</th>
                    <th>海外仓库存</th>
                    <th>FBA在途</th>
                    <th>海外仓在途</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(item, i) in stock_data"
                    :key="i"
                    :style="{
                      color:
                        item.estimate_sell_out_days > 90 ||
                        item.estimate_sell_out_days < 30
                          ? '#d90234'
                          : 'black'
                    }"
                  >
                    <td>{{ item.sku }}</td>
                    <td>{{ item.product_name }}</td>
                    <td>{{ item.avg }}</td>
                    <td>{{ item.all_nums }}</td>
                    <td>{{ item.fba }}</td>
                    <td>{{ item.fbm }}</td>
                    <td>{{ item.on_way_fba }}</td>
                    <td>{{ item.on_way_fbm }}</td>
                  </tr>
                  <tr v-if="!stock_data.length">
                    <td colspan="8">暂无数据</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="pending_container table-box">
              <span>待入仓货柜</span>
              <table>
                <thead>
                  <tr>
                    <th>序号</th>
                    <th>柜号</th>
                    <th>sku</th>
                    <th>品名</th>
                    <th>仓库</th>
                    <th>预计送货日期</th>
                    <th>实际入仓日期</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, i) in arrival_data" :key="i">
                    <td>{{ i + 1 }}</td>
                    <td>{{ item.container }}</td>
                    <td>{{ item.sku }}</td>
                    <td>{{ item.product_name }}</td>
                    <td>{{ item.warehouse_name }}</td>
                    <td>{{ item.delivery_date }}</td>
                    <td>{{ item.warehousing_date }}</td>
                  </tr>
                  <tr v-if="!arrival_data.length">
                    <td colspan="7">暂无数据</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="sku_order table-box">
              <span>sku订单</span>
              <table v-if="sku_data.length">
                <thead>
                  <tr>
                    <th></th>
                    <th v-for="(item, i) in sku_data_head" :key="i">
                      {{ item }}
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(items, j) in sku_data" :key="j">
                    <td>{{ items.name }}</td>
                    <td v-for="(item, idx) in items.value" :key="idx">
                      <span v-if="item.cost != undefined">{{ item.cost }}</span>
                      <span v-else>{{ item.num }}</span>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="advertising_guide table-box">
              <div>
                <span>广告指导</span>
                <button @click="edit = true">编辑</button>
                <button @click="commitData">提交</button>
                <label
                  >手动组广告系数<input
                    @change="manualprice"
                    type="text"
                    v-model="Manual_group_coefficient"
                /></label>
                <label
                  >自动组广告系数<input
                    @change="autoprice"
                    type="text"
                    v-model="Automatic_coefficient"
                /></label>
              </div>
              <div>
                <table class="guiding_table">
                  <thead>
                    <tr>
                      <td></td>
                      <td>指导价（手动）</td>
                      <td>实际花费（手动）</td>
                      <td>操作</td>
                      <td>指导价（自动）</td>
                      <td>实际花费（自动）</td>
                      <td>操作</td>
                      <td>广告比</td>
                    </tr>
                  </thead>
                  <tbody>
                    <template v-if="advertising_guide_table.length">
                      <tr v-for="(item, j) in advertising_guide_table" :key="j">
                        <td>
                          <span>{{ item.dates }}</span>
                        </td>
                        <td>
                          <span>{{ item.guidance_price_manual }}</span>
                        </td>
                        <td>
                          <span
                            :style="{
                              color:
                                item.cost / item.guidance_price_manual < 0.8 ||
                                item.cost / item.guidance_price_manual > 1.2
                                  ? '#d90234'
                                  : 'black'
                            }"
                            >{{ item.cost }}</span
                          >
                        </td>
                        <td>
                          <span>{{ item.remark_manual }}</span>
                        </td>
                        <td>
                          <span>{{ item.guidance_price_auto }}</span>
                        </td>
                        <td>
                          <span>{{ item.cost }}</span>
                        </td>
                        <td>
                          <span>{{ item.remark_auto }}</span>
                        </td>
                        <td>
                          <span
                            :style="{
                              color:
                                item.advertisement_proportion > 0.12
                                  ? '#d90234'
                                  : 'black'
                            }"
                            >{{ item.advertisement_proportion }}</span
                          >
                        </td>
                      </tr>
                    </template>
                    <!-- 最近可编辑三行 -->
                    <template v-if="editline.length">
                      <tr v-for="(item, i) in editline" :key="'edit' + i">
                        <td>
                          <input
                            :style="{ 'z-index': edit ? 1 : -1 }"
                            type="text"
                            v-model="item.dates"
                          />
                          <span>{{ item.dates }}</span>
                        </td>
                        <td>
                          <input
                            type="text"
                            :style="{ 'z-index': edit ? 1 : -1 }"
                            v-model="item.guidance_price_manual"
                          />
                          <span>{{ item.guidance_price_manual }}</span>
                        </td>
                        <td>
                          <input
                            type="text"
                            :style="{ 'z-index': edit ? 1 : -1 }"
                            v-model="item.cost"
                          />
                          <span>{{ item.cost }}</span>
                        </td>
                        <td>
                          <input
                            type="text"
                            :style="{ 'z-index': edit ? 1 : -1 }"
                            v-model="item.remark_manual"
                          />
                          <span>{{ item.remark_manual }}</span>
                        </td>
                        <td>
                          <input
                            type="text"
                            :style="{ 'z-index': edit ? 1 : -1 }"
                            v-model="item.guidance_price_auto"
                          />
                          <span>{{ item.guidance_price_auto }}</span>
                        </td>
                        <td>
                          <input
                            type="text"
                            :style="{ 'z-index': edit ? 1 : -1 }"
                            v-model="item.cost"
                          />
                          <span>{{ item.cost }}</span>
                        </td>
                        <td>
                          <input
                            type="text"
                            :style="{ 'z-index': edit ? 1 : -1 }"
                            v-model="item.remark_auto"
                          />
                          <span>{{ item.remark_auto }}</span>
                        </td>
                        <td>
                          <input
                            type="text"
                            :style="{ 'z-index': edit ? 1 : -1 }"
                            v-model="item.advertisement_proportion"
                          />
                          <span
                            :style="{
                              color:
                                item.advertisement_proportion > 0.12
                                  ? 'red'
                                  : 'black'
                            }"
                            >{{ item.advertisement_proportion }}</span
                          >
                        </td>
                      </tr>
                    </template>
                  </tbody>
                </table>
              </div>
            </div>
            <div class="advertising_word table-box">
              <div class="top_opt">
                <span>广告词监控</span>
                <!-- spu:进入页面是选择的spu -->
                <div>
                  <span>spu</span><input type="text" v-model="spu" disabled />
                </div>
                <!-- 上传  -->
                <div>
                  <span>时间</span
                  ><input type="text" v-model="uploadTime" disabled />
                  <span @click="upload" class="uploadspan">上传</span>
                </div>
                <div class="edit_park">
                  <el-button @click="editRemarks" type="primary"
                    >编辑</el-button
                  >
                  <el-button @click="submitRemarks" type="primary"
                    >提交</el-button
                  >
                </div>
              </div>
              <div class="advertising_monitor_table">
                <div class="table_con">
                  <table>
                    <thead>
                      <tr>
                        <th>序号</th>
                        <th>关键词</th>
                        <th>匹配类型</th>
                        <th>建议竞价 (中位数)</th>
                        <th>关键词竞价</th>
                        <th>曝光量</th>
                        <th>点击次数</th>
                        <th>点击率</th>
                        <th>花费</th>
                        <th>每次点击费用</th>
                        <th>订单</th>
                        <th>销售额</th>
                        <th>ACOS</th>
                        <th>备注</th>
                      </tr>
                    </thead>
                    <tbody>
                      <template v-if="advertising_monitor_data.length">
                        <tr
                          v-for="(item, i) in advertising_monitor_data"
                          :key="i"
                          :style="{
                            'background-color':
                              keyword_row === i ? 'lightblue' : ''
                          }"
                        >
                          <td>
                            {{ i + 1 }}
                          </td>
                          <td
                            class="key_word_ceil"
                            @click="show_keyword(item.key_word, i)"
                          >
                            {{ item.key_word }}
                          </td>
                          <td>
                            {{ item.matching_type }}
                          </td>
                          <td>
                            {{ item.proposal_bidding }}
                          </td>
                          <td>
                            {{ item.key_word_bidding }}
                          </td>
                          <td>
                            {{ item.exposure }}
                          </td>
                          <td>
                            {{ item.click_num }}
                          </td>
                          <td>
                            {{ item.click_rate }}
                          </td>
                          <td>
                            {{ item.cost }}
                          </td>
                          <td>
                            {{ item.every_click_free }}
                          </td>
                          <td>
                            {{ item.order_num }}
                          </td>
                          <td>
                            {{ item.sales_volume }}
                          </td>
                          <td
                            :style="{
                              color:
                                item.acos_status === '0'
                                  ? 'black'
                                  : item.acos_status === '1'
                                  ? 'red'
                                  : 'green'
                            }"
                            class="option_td"
                            @click="acosColor(item, i)"
                          >
                            {{ item.acos }}
                          </td>
                          <td class="option_td">
                            <span class="remark_span">{{ item.remarks }}</span
                            ><input
                              @change="changeInput(i)"
                              class="remark_input"
                              :style="{ 'z-index': editRemark ? 1 : -1 }"
                              type="text"
                              v-model="item.remarks"
                            />
                          </td>
                        </tr>
                      </template>
                      <!-- 如果没有数据 -->
                      <tr v-else>
                        <td colspan="14">暂无数据</td>
                      </tr>
                    </tbody>
                  </table>
                  <div
                    :style="{ 'z-index': showAcosDialog ? 999 : -999 }"
                    class="acosChangeColor"
                  >
                    <h4>acos设置颜色</h4>
                    <el-select v-model="acos_color" placeholder="请选择颜色">
                      <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      >
                      </el-option>
                    </el-select>
                    <el-button @click="checkAcosColor" type="primary"
                      >确定</el-button
                    >
                  </div>
                </div>
              </div>
              <!-- 默认显示当天 某个关键词-->
              <div class="advertising_monitor_keyword">
                <!-- 上面点击的关键词 假设为cur_keyword -->
                <div>
                  <span>关键词</span
                  ><input disabled type="text" v-model="cur_keyword" />
                </div>
                <div class="m40">
                  <label>数据时间范围</label>
                  <!-- 放到方法中：选择一天或者一段时间，不超过10天，默认当天 -->
                  <el-date-picker
                    :picker-options="adv_PickerOptions"
                    value-format="yyyy-MM-dd"
                    v-model="advertising_value"
                    type="daterange"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    @change="changeDate3"
                  >
                  </el-date-picker>
                  <el-select
                    :disabled="isTimeFrameDisabled"
                    v-model="keyword_timeframe"
                    placeholder="请选择时间节点"
                  >
                    <el-option
                      v-for="item in timeframsObj"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    >
                    </el-option>
                  </el-select>
                  <el-button type="primary" @click="monitor_keyword_Search"
                    >查询</el-button
                  >
                </div>
              </div>
              <div class="advertising_monitor_chart">
                <div class="chartCon">
                  <div
                    ref="chart2"
                    style="width: 80%; height: 500px;margin:0 auto;"
                    class="chart_keyword"
                  ></div>
                </div>
              </div>
              <div class="lastchart">
                <div class="chartCon">
                  <div
                    ref="chart3"
                    style="width: 80%; height: 500px;margin:0 auto"
                    class="chart_Ad_Performance"
                  ></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 上传弹窗 -->
    <div class="upload_model" v-if="show_upload">
      <h4>上传数据</h4>
      <el-form :inline="true" :model="formData" class="demo-form-inline">
        <el-form-item label="站点">
          <el-input disabled v-model="formData.store"></el-input>
        </el-form-item>
        <el-form-item label="国家">
          <el-input disabled v-model="formData.country"></el-input>
        </el-form-item>
        <el-form-item label="spu">
          <el-input v-model="spu" disabled></el-input>
        </el-form-item>
        <!-- 新增日期，默认当天  字段times-->
        <el-form-item label="日期">
          <el-date-picker
            value-format="yyyy-MM-dd"
            v-model="upload_times"
            type="date"
            placeholder="选择日期"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item class="selectTimeFrame" label="时间点">
          <el-select v-model="timeframe" placeholder="请选择时间点">
            <el-option
              v-for="(item, i) in timeframsObj"
              :key="i"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <span class="uploadSpan">上传文件</span>
          <input
            @change="changeFile"
            ref="uploadinput1"
            type="file"
            value="上传文件"
            accept=".csv,application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
          />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">提交</el-button>
        </el-form-item>
      </el-form>
      <span class="close_btn" @click="show_upload = false">x</span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      upload_times: "",
      searchlist1: [],
