    monitor_keyword_Search() {
      if (
        this.formData.channel === "all" ||
        this.formData.store === "all" ||
        this.formData.country === "all" ||
        !this.spu ||
        !this.cur_keyword ||
        !this.keyword_dates
      ) {
        alert("请选择渠道、国家、站点、关键词及时间查询！");
        return;
      }
      if (this.keyword_dates.indexOf("_") > -1 && !this.keyword_timeframe) {
        alert("选择一段时间时，请选择节点!");
        return;
      }
      let formData = new FormData();
      this.keyword_dates = this.keyword_dates + this.keyword_timeframe;
      console.log(this.keyword_dates);
      formData.append("channel", this.formData.channel);
      formData.append("store", this.formData.store);
      formData.append("country", this.formData.country);
      formData.append("spu", this.spu);
      formData.append("key_word", this.cur_keyword);
      formData.append("dates", this.keyword_dates);
      let url = "server/order_management/advertising_monitor_data/";
      this.$axios.post(this._url2 + url, formData).then(res => {
        if (res.data.code !== 200) {
          alert("网页出错，请联系IT管理员！");
          return;
        }
        console.log("是否对象", typeof res.data);
        let curItem = {};
        if (typeof res.data === "object") {
          curItem = res.data.data[0];
        } else {
          let resData = eval("(" + res.data + ")");
          curItem = resData.data[0];
        }
        // 修改两个图表
        // 绘制表格二
        this.chart_option2.series = [];
        this.chart_option2.xAxis.data =
          curItem.proposal_bidding_list.data_labels; //x轴
        // 修改 this.proposal_bidding_list key_word_bidding_list
        this.proposal_bidding_list = curItem.proposal_bidding_list;
        this.key_word_bidding_list = curItem.key_word_bidding_list;
        this.initSeries2(this.chart_option2); // Y轴
        this.initEchart("chart2", this.chart_option2);
        // 绘制表格三
        this.chart_option3.series = [];
        this.initSelectObj3();
        // 修改y轴
        this.exposure_list = curItem.exposure_list;
        this.click_num_list = curItem.click_num_list;
        this.click_rate_list = curItem.click_rate_list;
        this.cost_list = curItem.cost_list;
        this.every_click_free_list = curItem.every_click_free_list;
        this.order_num_list = curItem.order_num_list;
        this.sales_volume_list = curItem.sales_volume_list;
        this.acos_list = curItem.acos_list;
        console.log("表格三");
        this.chart_option3.xAxis.data = this.exposure_list.data_labels;
        console.log(this.exposure_list.data_labels);
        this.initSeries3(this.chart_option3);
        this.initEchart("chart3", this.chart_option3);
      });
