    changeInput(idx) {
      // 编辑的索引
      if (this.changeIndexArr.indexOf(idx) === -1) {
        this.changeIndexArr.push(idx);
      }
      console.log(idx);
    },
    acosColor(rowItem, rowIndx) {
      if (!this.editRemark) return;
      if (this.changeIndexArr.indexOf(rowIndx) === -1) {
        this.changeIndexArr.push(rowIndx);
      }
      this.showAcosDialog = true;
      this.curAcosIndex = rowIndx;
      this.acos_color = this.advertising_monitor_data[
        this.curAcosIndex
      ].acos_status;
    },
    checkAcosColor() {
      // 确定颜色。 做一些操作。

      this.advertising_monitor_data[
        this.curAcosIndex
      ].acos_status = this.acos_color;
      console.log(this.acos_color);
      console.log(this.advertising_monitor_data[this.curAcosIndex].acos_status);
      this.showAcosDialog = false;
    },
    editRemarks() {
      // 备注
      this.editRemark = true;
    },
    submitRemarks() {
      this.editRemark = false;
      // 发起请求
      console.log(this.changeIndexArr);
      let arr = [];
      this.changeIndexArr.forEach(i => {
        let obj = {};
        let item = this.advertising_monitor_data[i];
        obj["id"] = item["id"];
        obj["acos_status"] = item["acos_status"];
        obj["remarks"] = item["remarks"];
        arr.push(obj);
      });
      let data = JSON.stringify(arr);
      let url = "server/order_management/advertising_monitor_edit/";
      let formData = new FormData();
      formData.append("data", data);
      this.$axios.post(this._url2 + url, formData).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          alert("提交成功");
        } else {
          alert("未提交成功");
        }
      });
    },
    commitData() {
      this.edit = false;
      // 发起编辑提交请求
      let formData = new FormData();
      let dataObj = [];
      if (this.editline.length) {
        this.editline.forEach(i => {
          i.channel = this.formData.channel;
          i.country = this.formData.country;
          i.store = this.formData.store;
          i.spu = this.spu;
        });
        let dataArr = [];
        this.editline.forEach(i => {
          let dataObj = {};
          for (let key in i) {
            console.log(i);
            if (key === "cost") {
              dataObj["actual_price_manual"] = i["cost"];
              dataObj["actual_price_auto"] = i["cost"];
            } else {
              dataObj[key] = i[key];
            }
          }
          dataArr.push(dataObj);
        });
        console.log(dataArr);
        let data = JSON.stringify(dataArr);
        formData.append("data", data);
        let url = "server/order_management/advertising_edit_submit/";
        this.$axios.post(this._url2 + url, formData).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            alert("提交成功");
          } else {
            alert("未提交成功");
          }
        });
      }
    },
    changeFile() {
      // input 上传表格
      this.uploadForm = new FormData();
      let files = this.$refs.uploadinput1.files[0];
      this.inputItem = this.$refs.uploadinput1;
      console.log(this.inputItem);
      this.file_name = files.name;
      this.uploadForm.append("file", files);
      this.uploadForm.append("channel", this.formData.channel);
      this.uploadForm.append("store", this.formData.store);
      this.uploadForm.append("country", this.formData.country);
      this.uploadForm.append("spu", this.spu);
      this.uploadForm.append("times", this.upload_times);
      this.uploadForm.append("dates", this.timeframe);
      console.log(this.uploadForm);
    },
    manualprice() {
      // 销售额 * 系数 = 指导价
      // Manual_group_coefficient
      if (this.editline.length) {
        this.editline[2].guidance_price_manual =
          this.editline[2].cost * this.Manual_group_coefficient;
      }
    },
    autoprice() {
      if (this.editline.length) {
        this.editline[2].guidance_price_auto =
          this.editline[2].cost * this.Automatic_coefficient;
      }
    },
    show_keyword(keyword, rowIndex) {
      // 传入的keyword
      // 获取相对应的数据
      this.keyword_row = rowIndex;
      this.cur_keyword = keyword;
      if (!this.keyword_dates) {
        let date = new Date();
        let dateStr =
          date.getFullYear() +
          "-" +
          (date.getMonth() + 1) +
          "-" +
          date.getDate();
        console.log(dateStr);
        this.keyword_dates = dateStr;
      }
      this.monitor_keyword_Search();
    },
    edit_change() {
      this.edit = false;
    },
    changeOpt(val) {
      let that = this;
      // 第一次选择

      this.selectDefaultSpu = true;
      console.log("第一次选择", this.selectDefaultSpu);
      // 选择渠道
      if (val === "channel") {
        if (that.formData.channel === "all") {
          that.gm_list = [];
          that.mp_list = [];
        }
      } else if (val === "country") {
        that.stores = [];
        that.formData.store = "all";
        that.gm_list = [];
        that.mp_list = [];
        if (this.formData.country !== "all") {
          // let url =
          //   "http://106.52.43.196:8003/server/personnel_management/get_sign_area/";
          let url = this._url2 + "server/personnel_management/get_sign_area/";
          this.$axios
            .get(url, {
              params: {
                country: this.formData.country
              }
            })
            .then(res => {
              if (res.data.code !== 200) {
                alert("网页出错，请联系IT管理员！");
                return;
              }
              this.stores = res.data.data;
            });
        }
      } else if (val === "store") {
        if (
          this.formData.country !== "all" &&
          this.formData.channel !== "all" &&
          this.formData.store !== "all"
        ) {
          // 发起spu请求
          let url = this._url2 + "server/order_management/get_spu/";
          this.$axios
            .get(url, {
              params: {
                channel: this.formData.channel,
                store: this.formData.store,
                country: this.formData.country
              }
            })
            .then(res => {
              if (res.data.code !== 200) {
                alert("网页出错，请联系IT管理员！");
                return;
              }
              console.log(res);
              this.gm_list = res.data.gm_list;
              this.mp_list = res.data.mp_list;
              // 默认请求第一个魔片数据
              if (this.selectDefaultSpu && this.mp_list.length > 0) {
                this.selectMenu("1-1");
                this.selectDefaultSpu = false;
              }
            });
        }
      }
    },
    showSku(index, sku) {
      this.curIndex = index;
      this.sku = sku;
      let opt = "";
      if (!this.spu_sku_list.length) {
        opt = this.AllData.filter(i => i.SKU == sku);
      } else {
        opt = this.spu_sku_list.filter(
          i => i.SPU === this.spu && i.SKU === sku
        );
      }
      console.log("选择opt对象：", opt);
      this.url = opt[0].URL;
      // 最大值，最小值
      this.sku_heighest_30 = opt[0].maxs;
      this.sku_lowest_30 = opt[0].mins;
      this.chart_option1.series = [];
      // this.chart_option1.legend.data = opt[0].SKU;
      this.chart_option1.xAxis.data = [...opt[0].seven_labels];
      (this.chart_option1.xAxis.axisLabel = {
        interval: 0,
        rotate: 40
      }),
        (this.chart_option1.yAxis.min = Number(opt[0].min));
      this.chart_option1.yAxis.max = Number(opt[0].max);
      this.chart_option1.yAxis.interval = Number(opt[0].stepSize);
      this.chart_option1.series.push({
        name: opt[0].SKU,
        type: "line",
        stack: opt[0].SKU,
        data: opt[0].seven_data.map(i => Number(i)),
        label: {
          normal: {
            show: true,
            position: "top"
          }
        }
      });
      this.my_chart1.setOption(this.chart_option1, true);
    },
    changeEchartData(chartOpt, options, title) {
      // 传入echart及参数
      // 添加sku标题
      console.log(chartOpt);
      // 不需要标题
      // chartOpt.legend["data"].push(options[title]);
      chartOpt.xAxis["data"] = [...options.seven_labels];
      chartOpt.yAxis["min"] = Number(options.min);
      chartOpt.yAxis["max"] = Number(options.max);
      chartOpt.yAxis["interval"] = Number(options.stepSize);
      chartOpt.series.push({
        name: options[title],
        type: "line",
        stack: options[title],
        data: options.seven_data.map(i => Number(i)),
        label: {
          normal: {
            show: true,
            position: "top"
          }
        }
      });
    },
    clearEchart(chart, option, title_) {
      option.series = [];
      option.legend.data = [];
      option.xAxis.data = [];
      option.xAxis.axisLabel = { interval: 0, rotate: 40 };
      option.yAxis.min = "";
      option.yAxis.max = "";
      option.yAxis.interval = "";
      option.series = [];
      chart.setOption(option, true);
      // 清除画布
      chart.clear();
    },
    selectMenu(index, path) {
      // console.log(index);
