      this.loading = true;
      this.curIndex = 0;
      // 清空所有时间选择
      this.dateValue1 = ["", ""];
      this.dateValue = ["", ""];
      this.advertising_value = ["", ""];

      // 修改！！！
      this.clearEchart(this.my_chart1, this.chart_option1, "排名");
      let [idx, jdx] = index.split("-");
      jdx = parseInt(jdx);
      idx == "1"
        ? (this.spu = this.mp_list[jdx - 1])
        : (this.spu = this.gm_list[jdx - 1]);
      // 发起请求,可能传输数据： formData+spu字段
      console.log("spu", this.spu);
      let url = "server/order_management/get_data/";
      this.$axios
        .get(this._url2 + url, {
          params: {
            channel: this.formData.channel,
            store: this.formData.store,
            country: this.formData.country,
            spu: this.spu
          }
        })
        .then(res => {
          if (res.data.code !== 200) {
            alert("网页出错，请联系IT管理员！");
            return;
          }
          let result;
          if (typeof res.data !== "object") {
            result = eval("(" + res.data + ")");
          } else {
            result = res.data;
          }
          console.log(result);
          // 清空最大值，最小值：
          this.sku_heighest_30 = "";
          this.sku_lowest_30 = "";
          this.heighest_30 = "";
          this.lowest_30 = "";

          this.score_table = result.star || []; // 评分
          // 最高评分  result.star_max
          // 最低评分 result.star_min
          this.heighest_30 = result.star_max;
          this.lowest_30 = result.star_min;
          this.operate_report_table = [result.report] || []; // 运营日报
          this.AllData = result.rank_data || []; //  前台检查
          console.log("排名数据", this.AllData);
          // sku选择
          if (this.AllData.length > 0) {
            this.curIndex = 0;
            this.url = this.AllData[0].URL;
          }

          this.arrival_data = result.arrival_data || []; // 待入仓货柜
          this.sku_data = result.sku_data.data || []; // sku数据
          this.sku_data_head = result.sku_data.header || [];
          this.stock_data = result.stock_data || []; // 库存
          this.advertising_guide_table = result.advertisement_data || []; // 广告
          this.advertising_monitor = result.advertising_monitor || {}; // 广告监控

          this.advertising_monitor_data = this.advertising_monitor.datas || []; // 广告监控数据
          if (this.advertising_monitor_data.length > 0) {
            this.cur_keyword = this.advertising_monitor_data[0].key_word;
          }
          this.proposal_bidding_list =
            this.advertising_monitor.proposal_bidding_list || []; // 建议竞价
          this.key_word_bidding_list =
            this.advertising_monitor.key_word_bidding_list || []; // 关键词竞价
          this.click_num_list = this.advertising_monitor.click_num_list || [];
          // 点击量
          this.exposure_list = this.advertising_monitor.exposure_list || []; // 曝光量
          this.click_rate_list = this.advertising_monitor.click_rate_list || []; // 点击率
          this.cost_list = this.advertising_monitor.cost_list || []; // 花费
          this.order_num_list = this.advertising_monitor.order_num_list || []; // 订单
          this.sales_volume_list =
            this.advertising_monitor.sales_volume_list || []; // 销售额
          this.acos_list = this.advertising_monitor.acos_list || []; // acos
          // 自动组、手动组系数
          this.Manual_group_coefficient =
            result.advertisement_data[0].advertisement_coefficient_manual;
          this.Automatic_coefficient =
            result.advertisement_data[0].advertisement_coefficient_auto;

          // 绘制默认表格
          // 表格一
          if (result.rank_data.length) {
            let activeItem = result.rank_data[0];
            // 最大值，最小值
            this.sku_heighest_30 = activeItem.maxs;
            this.sku_lowest_30 = activeItem.mins;
            this.sku = activeItem.SKU;
            this.changeEchartData(this.chart_option1, activeItem, "SKU");
            this.my_chart1.setOption(this.chart_option1, true);
          }
          // 绘制表格二
          this.chart_option2.series = [];
          this.chart_option2.xAxis.data = this.proposal_bidding_list.data_labels;
          this.initSeries2(this.chart_option2);
          this.initEchart("chart2", this.chart_option2);

          // 绘制表格三
          this.chart_option3.series = [];
          this.chart_option3.xAxis.data = this.exposure_list.data_labels;
          this.initSelectObj3();
          this.initSeries3(this.chart_option3);
          this.initEchart("chart3", this.chart_option3);
          this.loading = false;
        });
    },
    changeDate(val) {
      console.log(val);
      let arr = val.map(i => {
        return i.split("-").join(".");
      });
      this.score_timevalue = arr;
      // console.log(this.score_timevalue);
      // 请求不同日期评分数据
      let formData = new FormData();
      formData.append("channel", this.formData.channel);
      formData.append("store", this.formData.store);
      formData.append("country", this.formData.country);
      formData.append("spu", this.spu);
      formData.append("start_date", this.score_timevalue[0]);
      formData.append("end_date", this.score_timevalue[1]);
      let url = "server/order_management/get_star_data/";
      this.$axios.post(this._url2 + url, formData).then(res => {
        if (res.data.code !== 200) {
          alert("网页出错，请联系IT管理员！");
          return;
        }
        // 请求的评分数据
        console.log(res.data.data);
        this.heighest_30 = res.data.data.maxs;
        this.lowest_30 = res.data.data.mins;
        this.score_table = res.data.data.data;
        this.dateValue1 = ["", ""];
      });
    },
    changeDate2(val) {
      let arr = val.map(i => {
        return i.split("-").join(".");
      });
      this.rank_timevalue = arr;
      // 发请求
      let url = "server/order_management/get_rank_data/";
      let formData = new FormData();
      formData.append("channel", this.formData.channel);
      formData.append("store", this.formData.store);
      formData.append("country", this.formData.country);
      formData.append("spu", this.spu);
      formData.append("start_date", this.rank_timevalue[0]);
      formData.append("end_date", this.rank_timevalue[1]);
      this.$axios.post(this._url2 + url, formData).then(res => {
        if (res.data.code !== 200) {
          alert("网页出错，请联系IT管理员！");
          return;
        }

        // 存储当个spu下的请求时间段的所有sku
        this.spu_sku_list = res.data.data;
        console.log(this.spu, this.sku);
        // 清空画布
        this.clearEchart(this.my_chart1, this.chart_option1, "排名");
        console.log(this.spu_sku_list);
        let opt = this.spu_sku_list.filter(
          i => i.SPU === this.spu && i.SKU === this.sku
        );
        this.chart_option1.series = [];
        this.chart_option1.legend.data = opt[0].SKU;
        this.chart_option1.xAxis.data = [...opt[0].seven_labels];
        (this.chart_option1.xAxis.axisLabel = { interval: 0, rotate: 40 }),
          (this.chart_option1.yAxis.min = Number(opt[0].min));
        this.chart_option1.yAxis.max = Number(opt[0].max);
        this.chart_option1.yAxis.interval = Number(opt[0].stepSize);
        console.log("最大值，最小值", opt[0].maxs, opt[0].mins);
        this.sku_heighest_30 = opt[0].maxs;
        this.sku_lowest_30 = opt[0].mins;
        this.chart_option1.series.push({
          name: opt[0].SKU,
          type: "line",
          stack: opt[0].SKU,
          data: opt[0].seven_data.map(i => Number(i)),
          label: {
            normal: {
              show: true,
              position: "top"
            }
          }
        });
        this.my_chart1.setOption(this.chart_option1, true);
      });
    },
    changeDate3() {
      if (this.advertising_value === null) return;
      console.log("时间", this.advertising_value);
      if (this.advertising_value[0] === this.advertising_value[1]) {
        console.log("选择时间相同");
        this.keyword_dates = this.advertising_value[0];
        this.isTimeFrameDisabled = true;
      } else {
        this.keyword_dates =
          this.advertising_value[0] + "_" + this.advertising_value[1] + "T";
        this.isTimeFrameDisabled = false;
      }
      let formData = new FormData();
    },
    initEchart(chart, option) {
      const chart_ = this.$refs[chart];
      if (chart_) {
        this["my_" + chart] = this.$echarts.init(chart_);
        this["my_" + chart].setOption(option, true);
        window.addEventListener("resize", () => {
          this["my_" + chart].resize();
        });
      }
      this.$on("hook:destoryed", () => {
        window.removeEventListener("resize", () => {
          this["my_" + chart].resize();
        });
      });
    },
    getEchartData() {
      // 初始化 my_chart1
      this.initEchart("chart1", this.chart_option1);
      console.log(this.my_chart1);
      // 初始化 my_chart2
      this.initEchart("chart2", this.chart_option2);
      // 初始化 my_chart3
      this.initEchart("chart3", this.chart_option3);
    },
    upload() {
      this.show_upload = true;
    },
    onSubmit() {
      if (
        this.uploadForm === null ||
        this.formData.store === "all" ||
        this.formData.country === "all" ||
        !this.spu
      ) {
        alert("请检查是否缺少国家、站点、spu、文件等数据");
        return;
      }
      // 发送请求
      let url = "server/order_management/advertising_monitor_upload/";
      console.log("文件内容", this.uploadForm);
      this.$axios.post(this._url2 + url, this.uploadForm).then(res => {
        if (res.data.code !== 200) {
          alert("网页出错，请联系IT管理员！");
          return;
        }

        this.uploadTime = this.timeframe;
        this.uploadForm = null;
        console.log(res.data.code);
        if (res.data.code === 403) {
          let coverflag = confirm("该节点数据已上传，是否覆盖");
          if (!coverflag) {
            // 不覆盖
            console.log("不覆盖");
            return;
          } else {
            console.log("覆盖");
            // 覆盖
            this.uploadForm = new FormData();
            console.log(123);
            let files = this.inputItem.files[0];
            if (files.name) {
              this.file_name = files.name;
            }
            this.uploadForm.append("file", files);
            this.uploadForm.append("channel", this.formData.channel);
            this.uploadForm.append("store", this.formData.store);
            this.uploadForm.append("country", this.formData.country);
            this.uploadForm.append("spu", this.spu);
            // 新增 times
            this.uploadForm.append("times", this.upload_times);
            this.uploadForm.append("dates", this.timeframe);
            this.uploadForm.append("cover", 1);
            this.$axios.post(this._url2 + url, this.uploadForm).then(res => {
              if (res.data.code !== 200) {
                alert("网页出错，请联系IT管理员！");
                return;
              }

              console.log("请求成功", res, res.data.data);
              this.advertising_monitor_data = res.data.data;
              this.cur_keyword = this.advertising_monitor_data[0].key_word;
            });
          }
        } else {
          //todo
          console.log("请求成功", res);
          this.advertising_monitor_data = res.data.data;
          this.cur_keyword = this.advertising_monitor_data[0].key_word;
        }
      });
      // 上传数据
      this.show_upload = false;
      // 上传信息
      this.lastUploadInfo.timeframe = this.timeframe;
    },
    dateFormat() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      this.upload_times = year + "-" + month + "-" + day;
    }
  },

  //进来请求channel参数
  created() {
    this.dateFormat();
    // 公共参数：server/order_management/get_channel_site_country/
    // let url = this._url2 + "server/personnel_management/get_sign_country/";
    let url = this._url2 + "server/personnel_management/get_sign_country/";
    this.$axios.get(url).then(res => {
      if (res.data.code !== 200) {
        alert("网页出错，请联系IT管理员！");
        return;
      }
      // res.data.data ： 渠道站点 res.data.data1：渠道站点国家
      console.log(res.data.data, res.data.data1);
      this.channels.push(res.data.data1);
      this.countries = res.data.data;
      this.formData.channel = this.channels[0];
      this.changeOpt("channel");
      this.formData.country = "美国";
      this.changeOpt("country");
      this.formData.store = "爱瑙";
      this.changeOpt("store");
    });
  },
  mounted() {
    // 挂载时初始化echart实例
    this.getEchartData();
  }
};
</script>

